













































import {
  computed,
  defineComponent,
  ref,
  PropType,
  onMounted
} from "@vue/composition-api";
import { useInputRules } from "@/ts/functions/composition/inputRules";
import ChooseImage from "@c/organisms/media/ChooseImage.vue";
import ImageModel from "@/ts/models/imageClass";

export default defineComponent({
  name: "ImageInput",

  props: {
    image: {
      type: [Object as () => ImageModel, String],
      default: undefined
    },
    size: {
      type: String,
      default: "150px"
    },
    default_image: {
      type: String,
      default: undefined
    },
    circle: {
      type: Boolean,
      default: false
    },
    nullable: {
      type: Boolean,
      default: false
    }
  },

  components: {
    ChooseImage
  },

  setup(props, { emit }) {
    var pickingImage = ref(false);

    const update = (img: string) => {
      emit("update", img);
    };

    const imageSrc = computed(() => {
      if (typeof props.image === "string") {
        return props.image;
      } else if (props.image) {
        return props.image.medium;
      } else {
        const defaultModel = new ImageModel();
        return defaultModel.medium;
      }
    });

    const rules = ref({
      image: useInputRules().image
    });

    return {
      rules,
      pickingImage,
      update,
      imageSrc
    };
  }
});
